import React, {Component} from 'react';
import Card from "./product/Card";
import Select from 'react-select'
import ReactMultiSelectCheckboxes from 'react-multiselect-checkboxes';

class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            furniture_styles: [],
            products: [],

        }
        this.filterList = this.filterList.bind(this);
    }

    //criteria filtering furniture style
    options_style = [
        {label: 'Classic ', value:"Classic" },
        {label: 'Midcentury', value: "Midcentury"},
        {label: 'Scandinavian', value: "Scandinavian"},
        {label: 'Modern', value: "Modern"},
        {label: 'Contemporary', value: "Contemporary"},
    ];

    //criteria Filtering delivery time
    options_time = [
        {value: '7', label: '1 week'},
        {value: '14', label: '2 week'},
        {value: '31', label: '1 month'},
        {value: '32', label: 'more '}
    ]

    //filter event
    filterList(e) {

        let updateList = this.state.furniture_styles;
        let products = this.state.products;
        let keysearch = e.target.value.toLowerCase();
        let productfiltered = [];

        productfiltered = this.filterByProperty(products, keysearch)
        this.setState({
            products: productfiltered,

        });
    }

    //helper Function for filtering multidimensi array
    filterByProperty(array, value) {
        var filtered = [];
        for (var i = 0; i < array.length; i++) {

            var obj = array[i];

            for (var key in obj) {
                // console.log(obj['furniture_style']);
                if (typeof (obj[key] === "object")) {
                    var item = obj[key];

                    if (item.toString().toLowerCase().search(value.toLowerCase()) !== -1) {
                        filtered.push(obj);
                    }
                }
            }
        }
        return filtered;
    }

    componentDidMount() {
        fetch('https://www.mocky.io/v2/5c9105cb330000112b649af8')
            .then(res => res.json())
            .then((data) => {
                this.setState({
                    furniture_styles: data.furniture_styles,
                    products: data.products,
                })
            })
            .catch(console.log)

        this.setState({
            products: this.state.products

        });

    }


    render() {
        return (
            <div className="container">
                <div className="jumbotron bg-primary">
                    <h3 className="text-warning font-italic">Fabelio E-Coomerce</h3>
                    <span className="row">
                        <span className="col-md-4">
                            <input className="form-control" type="search" placeholder="Search Furniture"
                                   aria-label="Search" onChange={this.filterList}/>
                        </span>
                    </span>
                    <br/>
                    <span className="row">
                        <span className="col-md-6">
                            <ReactMultiSelectCheckboxes  options={this.options_style}/>

                        </span>
                        <span className="col-md-2"></span>
                        <span className="col-md-4 pull-right">
                            <Select placeholder="Delivery Time" options={this.options_time}/>
                        </span>
                    </span>

                </div>


                <div className="card-deck">
                    {
                        this.state.products.map((products, i) => (
                            <div key={i} className="col-md-4">
                                <Card furniture_style={products.furniture_style} name={products.name}
                                      price={products.price} delivery_time={products.delivery_time}
                                      description={products.description}/><br/>
                            </div>
                        ))
                    }
                </div>


                <footer class="page-footer font-small bg-primary blue">

                    <div class="footer-copyright text-center py-3"><p className="text-warning"> Â© 2020 Copyright:
                        <a href="#" className="text-warning"> <br/> Ade Pamungkas</a></p>
                    </div>


                </footer>
            </div>
        );
    }
}

export default App;